}

.profile__main-text {
  cursor: pointer;
  position: relative;
  font-family: "Raleway", sans-serif;
  font-weight: 500;
  font-size: 18px;
  line-height: 100%;
  font-weight: 500;
  letter-spacing: -1.2px;
  margin-block-end: 9px;
  margin-inline-start: -1px;
}
.profile__main-text:hover {
  color: #337d5a;
}
.profile__main-text:hover::after {
  position: absolute;
  content: "";
  background-color: #337d5a;
  height: 2px;
  width: 100%;
  left: 2px;
  bottom: -3px;
}
.profile__main-text.active::after {
  position: absolute;
  content: "";
  background-color: #337d5a;
  height: 2px;
  width: 100%;
  left: 2px;
  bottom: -3px;
}
@media (max-width: 768px) {
  .profile__main-text {
    font-size: 14px;
  }
}

.profile__main-title--profile {
  margin-block-start: 35px;
  margin-block-end: 22px;
}

